<template>
    <div class="db-search-panel">
        <div class="db-header-bar">
            <span class="db-close-talion" @click="closeSearch">关闭</span>
            <form class="db-search-layout" onsubmit="return false">
                <input type="search" name="query" v-model.trim.lazy="queryStr" @keyup.enter="goSearch()">
            </form>
        </div>
        <ul class="db-plate-list">
            <li>
                <div>
                    <a href="#" >
                        <strong style="color: rgb(35, 132, 232);">电影</strong>
                        <span>影院热映</span>
                    </a>
                    <a href="#" >
                        <strong style="color: rgb(230, 70, 126);">同城</strong>
                        <span>周末活动</span>
                    </a>
                    <a href="#" >
                        <strong style="color: rgb(159, 120, 96);">阅读</strong>
                        <span>电子书</span>
                    </a>
                    <a href="#" >
                        <strong style="color: rgb(225, 100, 77);">东西</strong>
                        <span>心爱之物</span>
                    </a>
                </div>
            </li>
            <li>
                <div>
                    <a href="#" >
                        <strong style="color: rgb(122, 106, 219);">电视</strong>
                        <span>正在热播</span>
                    </a>
                    <a href="#" >
                        <strong style="color: rgb(42, 184, 204);">小组</strong>
                        <span>志趣相投</span>
                    </a>
                    <a href="#" >
                        <strong style="color: rgb(87, 116, 197);">游戏</strong>
                        <span>虚拟世界</span>
                    </a>
                    <a href="#" >
                        <strong style="color: rgb(64, 207, 169);">FM</strong>
                        <span>红心歌单</span>
                    </a>
                </div>
            </li>
            <li>
                <div>
                    <a href="#" >
                        <strong style="color: rgb(159, 120, 96);">图书</strong>
                        <span>畅销排行</span>
                    </a>
                    <a href="#" >
                        <strong style="color: rgb(244, 143, 46);">音乐</strong>
                        <span>新碟榜</span>
                    </a>
                    <a href="#" >
                        <strong style="color: rgb(89, 108, 221);">应用</strong>
                        <span>玩手机</span>
                    </a>
                    <a href="#" >
                        <strong style="color: rgb(66, 189, 86);">市集</strong>
                        <span>购买原创</span>
                    </a>
                </div>
            </li>
        </ul>
        <div class="db-search-login">
            <ul class="ul-li-float clearFix">
                <li><a href="">注册帐号</a></li>
                <li><router-link :to="{name:'loginPage'}" replace>登录豆瓣</router-link></li>
                <li><a href="">使用桌面版</a></li>
                <li><a href="">使用豆瓣App</a></li>
            </ul>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'db-search-panel',
        data() {
            return {
                queryStr: ''
            }
        },
        methods: {
            closeSearch: function () {
                this.$emit('searchShow',{
                    showType:false
                });
            },
            /**
             * 路由动态进入搜索页面
             * params: 搜索文字
             */
            goSearch: function () {
                this.$emit('searchShow',{
                    showType:false
                });
                this.$router.push({
                    name: 'searchPage',
                    query: {
                        queryStr: this.queryStr
                    }
                })
            }
        }
    }
</script>

<style lang="scss" scoped>
    .db-search-panel {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: #fff;
        z-index: 100;
        .db-header-bar {
            display: flex;
            padding: 0 1.8rem;
            height: 4.8rem;
            align-items: center;
            background: #fff;
            border-bottom: 0.1rem solid #f3f3f3;
        }
        .db-close-talion {
            color: #42bd56;
            font-size: 1.6rem;
        }
        .db-search-layout {
            flex: 1;
            width: 100%;
            padding-left: 1rem;
        }
        input[type=search] {
            width: 100%;
            height: 3.2rem;
            font-size: 1.4rem;
            border: 0;
            background: url(data:image/png;base64,) no-repeat 50% #f3f3f3;
            background-size: 1.73rem;
            border-radius: 0.3rem;
            box-sizing: border-box;
        }

        input[type=search]:focus {
            background: #f3f3f3;
        }
        .db-plate-list {
            display: flex;
            justify-content: space-around;
            padding-top: 2.5rem;
            li {
                width: 33.33%;
            }

            div {
                position: relative;
                float: left;
                left: 50%;
                top: 0;
            }

            a {
                position: relative;
                left: -50%;
                top: 0;
                display: block;
                margin-bottom: 2.5rem;
                text-align: center;
            }
            strong {
                display: block;
                font-weight: 400;
                font-size: 2.4rem;
                line-height: 2.8rem;
            }

            span {
                display: block;
                color: #999;
                height: 1.7rem;
                font-size: 1.2rem;
                letter-spacing: 0.1rem;
            }
        }
        .db-search-login {
            ul {
                border-bottom: 0.1rem solid #f3f3f3;
            }
            li {
                width: 50%;
                padding: 1.3rem 0;
                border-top: 1px solid #f3f3f3;
            }
            li:nth-child(odd){
                a{
                    border-right: 0.1rem solid #e5e5e5;
                }
            }
            a {
                display: block;
                font-size: 1.5rem;
                color: #42bd56;
                text-align: center;
                box-sizing: border-box;
            }
        }
    }
</style>



