<template>
    <div class="db-header-bar flexible-box">
        <h1 class="db-title"><router-link to="/">{{dbTitle}}</router-link></h1>
        <div class="db-tab-list adaptive-box-flex">
            <ul class="clearFix ul-li-float">
                <li >
                    <router-link :to="{name:'moviePage'}" style="color: #2384E8">电影</router-link>
                </li>
                <li >
                    <router-link :to="{name:'bookPage'}" style="color: #9F7860">图书</router-link>
                </li>
                <li >
                    <router-link :to="{name:'broadcastPage'}" style="color: #E4A813">广播</router-link>
                </li>
                <li >
                    <router-link :to="{name:'groupPage'}" style="color: #2AB8CC">小组</router-link>
                </li>
                <li>
                    <span class="db-search"  @click="showSearch"></span>
                </li>
            </ul>
        </div>
    </div>
</template>

<script type="text/javascript">
    export default {
        data() {
            return {
                dbTitle: '豆瓣'
            }
        },
        methods:{
            showSearch(){
                this.$emit('searchShow',{
                    showType:true
                });
            }
        }
    }
</script>

<style lang="scss" scoped="">
    .db-header-bar {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        align-items: center;
        padding: 0 1.8rem;
        height: 4.8rem;
        border-bottom: 0.1rem solid #f3f3f3;
        background: #fff;
        z-index: 10;
    }

    .db-header-bar {
        .db-title {
            width: 4.6rem;
            height: 2.2rem;
            margin-right: 1.8rem;
            font-size: 0;
            line-height: 3.2rem;
            color: #00b600;
            word-break: break-all;
            background: url(data:image/png;base64,) no-repeat;
            background-size: cover;
            a{
                display: block;
            }
        }
        .db-tab-list{
            display: flex;
            padding-top: 0.5rem;
            justify-content: flex-end;
        }
        .db-tab-list {
            li{
                font-size: 1.6rem;
                margin-right: 1.2rem;
                a{
                    display: block;
                }
            }
            .db-search{
                display: inline-block;
                width: 2.4rem;
                height: 1.8rem;
                background: url("data:image/png;base64,") no-repeat;
                background-size: cover;
            }
        }
    }
</style>



