<template>
    <div class="db-movie-rating" v-if="rating">
        <template v-if="fullStar === 0">
            <span class="db-movie-zero">暂无评分</span>
        </template>
        <template v-else>
            <template v-for="n in fullStar">
                <span class="db-full-star"></span>
            </template>
            <template v-for="n in halfStar">
                <span class="db-half-star"></span>
            </template>
            <template v-for="n in grayStar">
                <span class="db-gray-star"></span>
            </template>
            <span class="db-movie-average">{{average}}</span>
        </template>
        <slot name="ratingsCount"></slot>
    </div>
    　　
</template>

<script type="text/javascript">
    export default {
        data() {
            return {
                average:0,//电影评分
                fullStar: 0,//满颗星
                halfStar: 0,//半颗星
                grayStar: 0//半颗星
            }
        },
        props: {
            rating: {
                type: Object,
                required: true
            }
        },
        created() {
            this.average = this.rating.average;//获取电影评分
            this.fullStar = parseInt(this.average / 2) //计算满颗星星有多少
            this.halfStar = this.average % 2 === 0 ? 0 : 1;//计算半颗星星有多少
            this.grayStar = 5 - this.fullStar - this.halfStar;//计算灰色星星有多少
        }
    }
</script>

<style lang="scss" scoped="">
    .db-movie-rating{
        margin-top: 0.5rem;
        span{
            display: inline-block;
            width: 1.3rem;
            height: 1.3rem;
            font-size: 1.4rem;
            color: #aaa;
            vertical-align: middle;
        }
        .db-full-star{
            background-image: url(data:image/png;base64,);
            background-size: 1.3rem 1.3rem;
        }
        .db-half-star {
            background-image: url(data:image/png;base64,);
            background-size: 1.3rem 1.3rem;
        }
        .db-gray-star {
            background-image: url(data:image/png;base64,);
            background-size: 1.3rem 1.3rem;
        }
        .db-movie-average {
            padding: 0.2rem 0.8rem 0 0;
            width: auto;
            height: auto;
        }
        .db-movie-zero{
            width: auto;
            height: auto;
        }
    }
</style>



