<template>
    <transition name="slide-fade">
        <div class="db-login-view">
            <h1>
                <a @click="goHomeBack">取消</a>登录豆瓣
            </h1>
            <form method="get" @submit.prevent="onSubmit()">
                <div class="db-form-user">
                    <label>
                        <input v-model="userAccount" type="text" placeholder="邮箱 / 手机号 / 用户名">
                    </label>
                </div>
                <div class="db-form-pwd">
                    <input v-model.trim="userPassword" v-if="passType === 'password'" type="password"  placeholder="密码">
                    <input v-model.trim="userPassword" v-else type="text"  name="userPassword" placeholder="密码">
                    <span class="db-show-pwd" :class="{show: isShow}" @click="userShowPwd()"></span>
                </div>
                <div class="">
                    <button class="submit" type="submit" >{{loginState}}</button>
                </div>
            </form>
            <div class="db-login-footer">
                <div class="db-more-login">使用其他方式登录 &amp; 找回密码</div>
                <div class="db-login-btns">
                    <router-link :to="{name: 'registerPage'}">注册豆瓣帐号</router-link>
                    <a href="#">下载豆瓣App</a>
                </div>
            </div>
        </div>
    </transition>

</template>

<script>
    export default {
        name: 'loginPage',
        data () {
            return {
                loginState: '登录',
                userAccount:'',
                userPassword:'',
                passType:'password',
                isShow: false
            }
        },
        methods:{
            goHomeBack(){
                this.$root.$children[0].searchShowType = false;
                this.$router.push({
                    path: '/',
                })
            },
            userShowPwd(){
                this.isShow = !this.isShow;
                this.passType = this.passType == "password" ? "text" : "password";
            },
            onSubmit(){
                this.$userInfo.userAccount = this.userAccount;
                this.$userInfo.userPassword = this.userPassword;
                this.goHomeBack();
            }
        }
    }
</script>

<style lang="scss" scoped>
    .slide-fade-enter-active {
        transition: all .3s ease;
    }
    .slide-fade-leave-active {
        transition: all .5s cubic-bezier(1.0, 0.5, 0.8, 1.0);
    }
    .slide-fade-enter, .slide-fade-leave-to{
        transform: translateY(10px);
        opacity: 0;
    }
    .db-login-view {
        position: fixed;
        top:0;
        left: 0;
        right: 0;
        bottom: 0;
        background: #fff;
        z-index: 110;
        h1 {
            margin: 0 0 1rem 0;
            padding: 0 1.8rem;
            height: 4.5rem;
            line-height: 4.5rem;
            background: #fff;
            border-bottom: 0.1rem solid #eee;
            text-align: center;
            font-size: 1.8rem;
            font-weight: bold;
            a {
                position: absolute;
                left: 1.8rem;
                top: 0;
                color: #42bd56;
                font-size: 1.5rem;
                font-weight: normal;
            }
        }
        form {
            padding: 2rem 1.5rem;
            strong {
                font-size: 1.5rem;
                color: #222;
                display: none;
                margin-bottom: 0.5rem;
            }
            input[type="email"], input[type="text"], input[type="password"] {
                display: inline-block;
                width: 100%;
                height: 4.4rem;
                padding: 0 0.8rem;
                box-sizing: border-box;
                font-size: 1.5rem;
                background: #fff;
                border: 0.1rem solid #ccc;
                border-top-left-radius: 0.3rem;
                border-top-right-radius: 0.3rem;
            }
            .db-form-pwd {
                position: relative;
                input {
                    padding-right: 4rem;
                    border-top: 0;
                }
                .db-show-pwd {
                    position: absolute;
                    right: 0.2rem;
                    top: 0;
                    display: block;
                    height: 100%;
                    width: 3.2rem;
                    background-image: url(data:image/png;base64,);
                    background-repeat: no-repeat;
                    background-position: center center;
                    background-size: 2.2rem;
                    z-index: 3;
                }
                .db-show-pwd.show {
                    background-image: url(data:image/png;base64,);
                }
            }
            .submit {
                cursor: pointer;
                width: 100%;
                padding: 1.2rem 2.6rem;
                margin-top: 1rem;
                font-size: 1.7rem;
                text-align: center;
                color: #fff;
                background: #17AA52;
                border: 0.1rem solid #17AA52;
                border-radius: 0.3rem;
            }
        }

        .db-login-footer {
            .db-more-login {
                font-size: 1.5rem;
                color: #aaa;
                text-align: center;
            }

            .db-login-btns {
                margin-top: 4rem;
                text-align: center;
                font-size: 1.5rem;

                a {
                    color: #42bd56;
                    margin-right: 1.5rem;
                }
            }
        }
    }
</style>



