<template>
    <div class="db-register">
        <h1 class="title">欢迎加入豆瓣</h1>
        <form method="post" @submit.prevent="onSubmit()">
            <div class="form-alias">
                <input v-model.trim="userAccount" type="text" placeholder="邮箱 / 手机号 / 用户名">
            </div>
            <div class="form-pwd">
                <label>
                    <template v-if="passType === 'password'">
                        <input v-model.trim="userPassword" type="password" name="userPassword" placeholder="密码">
                    </template>
                    <template v-if="passType === 'text'">
                        <input v-model.trim="userPassword" type="text" name="userPassword" placeholder="密码">
                    </template>
                    <span class="show-pwd" :class="{'pwd-show': isShow}" @click="userShowPwd()"></span>
                </label>
            </div>
            <div class="form-name">
                <input v-model.trim="name" type="text"  name="name" placeholder="昵称">
            </div>
            <div class="form-submit">
                <button class="submit" type="submit" >{{registerState}}</button>
            </div>
        </form>
        <div class="footer">
            <div class="agreement">点击「注册」代表你已阅读并同意用户使用协议</div>
            <a class="open-app">下载豆瓣App</a>
        </div>
    </div>
</template>

<script type="text/javascript">
    export default {
        data() {
            return {
                registerState:'立即注册',
                passType:'password',
                userAccount:'',
                userPassword:'',
                name:'',
                isShow:false
            }
        },
        methods:{
            goHomeBack(){
                this.$root.$children[0].searchShowType = false;
                this.$router.push({
                    path: '/',
                })
            },
            userShowPwd(){
                this.isShow = !this.isShow;
                this.passType = this.passType == "password" ? "text" : "password";
            },
            onSubmit(){
                this.$userInfo.userAccount = this.userAccount;
                this.$userInfo.userPassword = this.userPassword;
                this.goHomeBack();
            }
        }
    }
</script>

<style lang="scss" scoped>
.db-register{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #fff;
    z-index: 110;
    .title{
        margin: 10% 0 9%;
        font-size: 4rem;
        font-weight: 300;
        color: #42bd56;
        text-align: center;
    }
    form{
        padding: 2rem 1.5rem;
        overflow: hidden;
    }
    .form-alias{
        border-top-left-radius: 0.3rem;
        border-top-right-radius: 0.3rem;
        border: 0.1rem solid #ccc;
    }
    .form-pwd{
        position: relative;
        border-left: 0.1rem solid #ccc;
        border-right: 0.1rem solid #ccc;
        .show-pwd{
            position: absolute;
            right: 0.2rem;
            top: 0;
            display: block;
            height: 100%;
            width: 3.2rem;
            background: url(data:image/png;base64,) center center no-repeat;
            background-size: 2.2rem;
            z-index: 3;
        }
        .pwd-show{
            background-image: url(data:image/png;base64,);
        }
    }
    .form-name{
        border-bottom-left-radius: 0.3rem;
        border-bottom-right-radius: 0.3rem;
        border: 0.1rem solid #ccc;
    }
    .form-submit{
        button{
            width: 100%;
            padding: 1.2rem 2.6rem;
            margin-top: 1rem;
            font-size: 1.7rem;
            text-align: center;
            color: #fff;
            background: #42bd56;
            border: 0.1rem solid #17AA52;
            border-radius: 0.3rem;
        }
    }
    input{
        display: inline-block;
        width: 100%;
        height: 4.4rem;
        padding: 0 0.8rem;
        font-size: 1.5rem;
        background: #fff;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
    }
    .agreement{
        font-size: 1.4rem;
        color: #aaa;
        text-align: center;
    }
    .open-app{
        display: block;
        margin-top: 4rem;
        text-align: center;
        font-size: 1.5rem;
        color: #42bd56;
    }
}
</style>



